@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?dkl18');
  src:  url('fonts/icomoon.eot?dkl18#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?dkl18') format('truetype'),
    url('fonts/icomoon.woff?dkl18') format('woff'),
    url('fonts/icomoon.svg?dkl18#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-search:before {
  content: "\e986";
}
.icon-heart:before {
  content: "\e9da";
}
.icon-checkmark:before {
  content: "\ea10";
}
.icon-play3:before {
  content: "\ea1c";
}
.icon-pause2:before {
  content: "\ea1d";
}
